@import"_nullStyle.scss";
.line-through{
    text-decoration: line-through;
    opacity: 0.5;
}
body{
    background-color: rgb(254, 218, 227);
}
.wrapper{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    .content{
        margin-top:10%;
        display: flex;
        flex-direction: column;
        align-items: center;
        h1{
            font-size: 56px;
            line-height: 56px;
            color: rgb(255, 57, 107);
        }
        .card{
            margin-top: 20px;
            background-color: rgb(239, 240, 232);;
            padding: 20px;
            border-radius: 5px;
            box-shadow: 1px 1px 5px rgb(184, 154, 154);
            display: flex;
            flex-direction: column;
            align-items: center;
            width:400px;
            .radio{
                width: 70%;
                display: flex;
                justify-content: space-between;
                span{
                    font-size: 16px;
                    color: rgb(255, 57, 107);
                    margin-left: 5px;
                }
            }
            .todos{
                width:100%;
                p{
                    margin: 15px 0;
                    font-size: 16px;
                    color: rgb(255, 110, 146);
                    text-align: center;
                }
                .todo{
                    cursor: pointer;
                    margin: 10px;
                    width: 100%;
                }
            }
        }
    }
}
.back-modal{
    position: fixed;
    background-color: rgba(18, 18, 18, 0.5);
    width:100%;
    height: 100%;
    z-index: 10;
    top:0px;
    left: 0px;
}
.modal{
    position: absolute;
    z-index: 20;
    background-color: white;
    padding: 20px;
    top: 50%;
    left:50%;
    transform:translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: end;
    border-radius: 5px;
    .modal-content{
        display: flex;
        flex-direction: column;
        align-items: center;
        .text-input{
            padding: 10px;
            border-radius: 5px;
            width: 300px;
            border: 1px solid rgb(255, 170, 191);
        }
    }
    .close{
        cursor: pointer;
        color:rgb(255, 57, 107);
        float:right;
        margin-bottom: 10px;
    }
}
button{
    background-color: rgb(255, 57, 107);
    color: #fff;
    padding: 10px 20px;
    border-radius: 100px;
    font-size: 16px;
    margin-top: 15px;
}
button:disabled{
    background-color: rgb(255, 172, 193);
}
.nothing{
    display: none;
}